// <div data-aspect-ratio="16:9"><iframe ...></iframe></div>
[data-aspect-ratio] {
    display: block;
    max-width: 100%;
    position: relative;
    overflow: hidden;

    &:before {
        content: '';
        display: block;
    }

    > * {
        display: block;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
    > .center--contain {
        left: 50%;
        top: 50%;
        height: auto;
        width: 100%;
        transform: translate(-50%, -50%)
    }
    > .center--cover {
        left: 50%;
        top: 50%;
        height: 100%;
        width: auto;
        transform: translate(-50%, -50%)
    }
}
[data-aspect-ratio="3:1"]:before {
    padding-top: 33.33%;
}
[data-aspect-ratio="2:1"]:before {
    padding-top: 50%;
}
